import { css, modifier, styled } from '@lokalise/styled';

import { value } from '../../utils/theme';

export const Container = styled.span`
	display: flex;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-items: center;
	padding: 0 0 0 1px;
`;

interface ButtonInterface {
	$selected: boolean;
}

export const Button = styled.button<ButtonInterface>`
	position: relative;
	display: inline-block;
	padding: 4px 7px 8px;
	height: 30px;
	z-index: 1;
	vertical-align: middle;
	margin: 0 0 0 -1px;
	cursor: pointer;

	background: transparent;
	border: 1px solid ${value('color.border.active')};
	font-size: 20px;
	color: ${value('color.text.link.default')};

	${modifier(
		'$selected',
		css`
			z-index: 2;

			background: ${value('color.background.action.primary.default')};
			color: ${value('color.text.inverse')};
		`,
	)}

	&:first-child {
		border-radius: ${value('radius.m')} 0 0 ${value('radius.m')};
	}
	&:last-of-type {
		border-radius: 0 ${value('radius.m')} ${value('radius.m')} 0;
	}

	&:hover,
	&:focus {
		background-color: ${value('color.background.surface.fore')};
		${modifier(
			'$selected',
			css`
				background: ${value('color.background.action.primary.default')};
			`,
		)};
	}
`;
